/*------------------------------------*\
	#BASE
\*------------------------------------*/

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 400;
	margin: 2.25rem 0 0;
}

h1,
.entry-title {
	font-size: 1.476rem;
	line-height: 2rem;
}

h2 {
	font-size: 1.383rem;
	line-height: 2rem;
}

h3 {
	font-size: 1.296rem;
	line-height: 1.8rem;
}

h4 {
	font-size: 1.215rem;
	line-height: 1.6rem;
}

h5 {
	font-size: 1.138rem;
	line-height: 1.4rem;
}

h6 {
	font-size: 1rem;
	line-height: 1.4rem;
}

h2 + h3 {
	margin-top: .8rem;
}

h3 + h4 {
	margin-top: .8rem;
}

h4 + h5 {
	margin-top: .8rem;
}

h5 + h6 {
	margin-top: .8rem;
}

p,
blockquote,
dl,
ol,
ul,
table,
figure {
	margin: .8rem 0 0;
	padding: 0;
}

b,
strong {
	-webkit-font-feature-settings: "smcp", "onum";
	font-feature-settings: "smcp", "onum";
	font-style: normal;
	font-weight: 400;
	letter-spacing: .06rem;
	text-transform: lowercase;
}

blockquote {
	margin-left: .375rem;
	padding-left: .8rem;
	position: relative;
}

blockquote::before {
	border: 1px solid #333;
	border-right: 0;
	content: '';
	height: 100%;
	left: 0;
	position: absolute;
	top: -.1rem;
	width: .2rem;
}

blockquote cite {
	display: block;
	margin-top: .375rem;
}

cite {
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	font-style: normal;
	letter-spacing: .06rem;
	text-transform: lowercase;
}

figure,
img {
	max-width: 100%;
	height: auto;
}

figcaption {
	margin: #{$line-height / 5}em 0 0;
	font-style: italic;
}

table {
	border-spacing: 0;
	width: 100%;
}

th {
	border-bottom: 2px solid;
	text-align: left;
	font-weight: normal;
}

td {
	border: 0;
	border-bottom: 1px solid;
	margin: 0;
}

tr:last-child td {
	border-bottom: 0;
}

td,
th {
	padding: .375rem;
}

dt,
th {
	letter-spacing: .06rem;
	text-transform: uppercase;
}

dd {
	margin-bottom: .8rem;
	margin-left: .8rem;
}

ul,
ol {
	padding-left: .8rem;
}

li {
	margin-top: .375rem;
}

ul ul,
ol ol,
ol ul,
ul ol {
	margin: 0;
}

ul > li {
	list-style-type: none;
	position: relative;
}

::-webkit-input-placeholder {
	opacity: 1;
}

:-moz-placeholder { /* Firefox 18- */
	opacity: 1;
}

::-moz-placeholder { /* Firefox 19+ */
	opacity: 1;
}

:-ms-input-placeholder {
	opacity: 1;
}

label {
	display: block;
}

/**
 * Remove the default vertical scrollbar in IE.
 */

textarea {
	overflow: auto;
	resize: vertical;
}

input:not([type=submit]),
textarea {
	background: none;
	border: 1px solid;
	padding: .375rem .375rem .1rem;
	width: 100%;
}

[type="submit"] {
	background: none;
	border: 1px solid;
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	letter-spacing: .06rem;
	margin-top: .375rem;
	padding: .2rem .375rem;
	text-transform: lowercase;
}

input:hover,
input:focus,
input:active,
textarea:hover,
textarea:focus,
textarea:active {
	border-left-width: 3px;
	outline: none !important;
	padding-left: calc(.375rem - 2px);
}

[type=submit]:hover,
[type=submit]:focus,
[type=submit]:active {
	border-right-width: 3px;
	padding-right: calc(.375rem - 2px);
}

/*------------------------------------*\
	#GLOBAL CLASSES
\*------------------------------------*/

.clearfix::after {
	clear: both;
	content: '';
	display: block;
}

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	height: 1px;
	overflow: hidden;
	position: absolute !important;
	width: 1px;
	/* many screen reader and browser combinations
	announce broken words as they would appear visually */
	word-wrap: normal !important;
}

.skip-link:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	height: auto;
	left: 5px;
	padding: .5rem .8rem;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar. */
}

.alignleft {
	display: block;
	float: left;
	margin-right: 1.125rem;
}

.alignright {
	display: block;
	float: right;
	margin-left: 1.125rem;
}

.aligncenter,
.alignnone {
	display: block;
	margin: 1.6rem 0;
}

.aligncenter {
	margin-left: auto;
	margin-right: auto;
}

.alignleft,
.alignright {
	margin-top: .4rem;
	margin-bottom: .8rem;
}

figure.alignleft,
figure.alignright {
	margin-top: 1.125rem;
}

.alignleft img,
.alignright img {
	margin: 0;
}

.wp-caption-text {
	font-style: italic;
	margin-top: .3rem;
}

.small-caps {
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	letter-spacing: .06rem;
	text-transform: lowercase;
}

.dropcap {
	float: left;
	-webkit-font-feature-settings: unset;
	font-feature-settings: unset;
	padding: 0px;
	font-size: 3.353em;
	height: 2.2802rem;
	line-height: .8em;
	margin-top: 0.45rem;
	text-transform: uppercase;
}

.gallery {
	margin-left: -.8rem;
	margin-right: -.8rem;
}

.gallery::after {
	clear: both;
	content: '';
	display: block;
}

.gallery-item {
	display: block;
	float: left;
	margin: .8rem;
}

.gallery-caption {
	display: none;
}

.gallery-size-large .gallery-caption {
	display: block;
}

/*------------------------------------*\
	#OBJECTS
\*------------------------------------*/

.site-header,
.content-wrapper {
	margin: 0 auto;
	max-width: 1200px;
	width: 85%;
}

.post,
.type-page {
	margin-top: 4.5rem;
	max-width: 37rem;
	margin-left: auto;
	margin-right: auto;
}

.alternate-layout .post,
.alternate-layout .page {
	max-width: inherit;
}

.sidebar-template .post,
.sidebar-template .page {
	max-width: inherit;
}

/*------------------------------------*\
	#COMPONENTS
\*------------------------------------*/

/**
 * Navigation and Pagination
 */

.primary-nav {
	display: inline-block;
	padding: 0;
}

.primary-nav li {
	margin: 0 .4rem .4rem;
}

.pagination {
	margin-top: 3.2rem;
}

.page-numbers {
	-webkit-font-feature-settings: "pnum";
	font-feature-settings: "pnum";
	margin: 0;
	padding: 0;
}

.page-numbers .next,
.page-numbers .prev,
.page-numbers span {
	font-feature-settings: "smcp";
	letter-spacing: .06rem;
	text-transform: lowercase;
}

.page-numbers::after {
	clear: both;
	content: '';
	display: block;
}

.page-numbers li {
	display: block;
	margin-top: 0;
	float: left;
}

.page-numbers li::before {
	display: none;
}

.entry-content .page-numbers {
	margin-top: 1.6rem;
}

.page-numbers li:not(:first-of-type) {
	margin-left: .8rem;
}

.footer-nav li::before {
	content: "–";
	left: -.8rem;
	top: 0;
	position: absolute;
}

/**
 * Site header
 */

.site-header {
	margin-top: 3.2rem;
	text-align: center;
}

.site-title {
	font-size: 1.575rem;
	line-height: 2rem;
	margin: 0;
}

.site-title a {
	background-position: 0 calc(100% + 2px);
	background-size: 100% 1px;
}

.site-title a:hover,
.site-title a:active,
.site-title a:focus {
	background-position: 0 calc(93% + 2px);
}

.site-description {
	color: #666;
	font-style: italic;
	margin-top: .375rem;
}

/**
 * Blog entry
 */

.entry-header {
	position: relative;
}

.entry-header .author-date {
	color: #666;
	font-style: italic;
}

.entry-header .author {
	display: inline;
	margin-top: 0;
}

.entry-header .date {
	display: inline;
}

.entry-header .date::before {
	content: " · ";
	font-style: normal;
}

.entry-header .featured {
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	letter-spacing: .03rem;
	position: absolute;
	left: 0;
	text-transform: lowercase;
	transform: translateY(-100%);
}

.entry-title {
	margin-top: 0;
}

.entry-title a,
.entry-header .date a {
	background-size: 100% 1px;
	background-position: 0 calc(100% + 2px);
}

.entry-title a:hover,
.entry-title a:active,
.entry-header .date a:hover,
.entry-header .date a:active {
	background-position: 0 93%;
}

.entry-title a:focus,
.entry-header .date a:focus {
	background-size: 100% 3px;
	background-position: 0 calc(93% + 2px);
}

.entry-content {
	margin-top: 1.2rem;
}

.entry-content ul > li::before {
	content: "–";
	left: -.8rem;
	top: 0;
	position: absolute;
}

.entry-footer {
	clear: both;
	margin-left: auto;
	margin-right: auto;
	padding-top: .4rem;
}

.entry-footer-content {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}

.entry-footer-block {
	box-sizing: border-box;
	color: #666;
	-webkit-font-feature-settings: "smcp", "onum";
	font-feature-settings: "smcp", "onum";
	letter-spacing: .06rem;
	line-height: 1.3rem;
	text-transform: lowercase;
	width: 100%;
}

.entry-footer-block + .entry-footer-block {
	text-indent: 0;
	margin-top: .8rem;
}

.entry-footer-block-label {
	display: block;
}

/**
 * Gutenberg blocks.
 */

@import 'blocks/frontend';

/**
 * Comments
 */

.comments-area,
.alternate-layout .comments-area {
	margin-left: auto;
	margin-right: auto;
}

.comments-area {
	max-width: 37rem;
}

.commentlist {
	padding-left: 0;
}

.comment {
	list-style-type: none;
	margin-top: 2.25rem;
}

.comment:first-of-type {
	margin-top: 0;
}

.comment-meta {
	max-width: 35rem;
	text-align: center;
}

.comment .avatar {
	display: block;
	margin: 0 auto;
}

.comment-author-name {
	display: block;
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	letter-spacing: .06rem;
	margin-top: .8rem;
	text-transform: lowercase;
}

.reply {
	display: inline-block;
	margin-top: .375rem;
}

.comment-date {
	margin-top: 0;
}

.comment-form p + p {
	text-indent: 0;
	margin-top: .8rem;
}

.comment-form-cookies-consent {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: baseline;
	-ms-flex-align: baseline;
	align-items: baseline;
}

.comment-form-cookies-consent label {
	margin-left: .5em;
}

.comment-form-cookies-consent input {
	width: auto;
}

.comment-navigation {
	margin-top: 1.6rem;
}

/**
 * Archive
 */

.archive-title {
	margin-top: 4.5rem;
}

/**
 * 404 template and no search results
 */

.content-404,
.no-search-results {
	max-width: 770px;
}

/**
 * Widgets
 */

.widget {
	margin-top: 2.25rem;
}

.site-footer .widget {
	box-sizing: border-box;
	float: left;
	-webkit-box-flex: 0;
	-ms-flex: 0 1 16.9rem;
	flex: 0 1 16.9rem;
	max-width: 16.9rem;
	padding: 0 1.2rem;
}

.widget-title {
	margin-top: 0;
}

.widget li::before {
	content: "–";
	left: -.8rem;
	top: 0;
	position: absolute;
}

.tagcloud {
	-webkit-font-feature-settings: "smcp";
	font-feature-settings: "smcp";
	letter-spacing: .06rem;
	text-transform: lowercase;
}

.widget_calendar tbody td,
.widget_calendar th {
	text-align: center;
}

.search-form {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-top: .8rem;
}

.search-form label {
	-webkit-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
}

.search-field {
	border: none;
	border-bottom: 1px solid;
}

.search-submit {
	height: 1.6rem;
	margin-left: .375rem;
	padding: 0;
	position: relative;
	top: .3rem;
	width: 1.6rem;
}

.search-submit:hover,
.search-submit:focus,
.search-submit:active {
	padding: 0;
	border: 0;
}

/**
 * Gallery
 */

.gallery a {
	border-bottom: none;
}

/**
 * Site footer
 */

.site-footer {
	margin: 4.5rem auto 3.2rem;
	text-align: center;
	width: 85%;
}

.footer-nav-container {
	display: inline-block;
}

.footer-nav {
	margin-top: 0;
